body {
  line-height: 24px;
  font-size: 13px;
  color: #333333;
  font-family: "Raleway", sans-serif;
  background-color: #fff;
}

/*--- Nav Scrolling Offset --*/

.offset:before {
  height: 4rem;
  margin-top: -4rem;
  content: "";
  display: block;
}

/*--- Extra Bootstrap Column Padding --*/
[class*="col-"] {
  padding: 1rem;
}

/*============= NAVIGATION =============*/
.navbar {
  padding: .7rem 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: .1rem;
  -webkit-transition: background-color .5s ease 0s;
  transition: background-color .5s ease 0s;
}
.navbar-brand h2 {
  cursor: pointer;
  text-transform: capitalize;
  color:white;
  font-weight: bold;
  letter-spacing: .3rem;
  /*text-shadow: 2px 2px #FF0000;*/
  text-shadow: 1.5px 1.5px grey;
  font-family: 'Courgette', cursive;
}
.svg-inline--fa.fa-w-14 {
  color: white;
  font-size: 1.6rem;
}
.navbar-nav li {
  padding-right: .8rem;
}
.navbar-nav .nav-link {
  color: white!important;
  padding-top: .8rem;
  font-weight: bold;
  text-shadow: 1px 1px black;
}
.navbar-nav .nav-link.active,
.navbar-nav .nav-link:hover {
  cursor: pointer;
  color: skyblue!important;
  font-weight : bold;
}
.navbar .solid {
  background: rgba(0, 0, 0, 0.7)!important;
  -webkit-transition: background-color 1s ease 0s;
  transition: background-color 1s ease 0s;
}

/*============= LANDING PAGE =============*/

.caption {
  width: 100%;
  max-width: 100%;
  position: absolute;
  top: 28%;
  z-index: 1;
  margin-bottom: 50rem;
}

.btn-lg {
  border-width: medium;
  border-radius: 0;
  padding: .8rem 1.5rem;
  font-size: 1.1rem;
  font-weight: 400;
  text-transform: uppercase;
  display: inline-block !important;

}

.main_title h2 {
  font-size: 36px;
  font-family: "source sans pro", sans-serif !important;
  color:rgb(102, 102, 102);;
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  line-height: 58px;
  padding: 0px 23px;
  font-weight: 100;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
#home-inner-tmp .overlay {
  position: absolute;
  background: #0c0808;
  opacity: 0.6;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

